import React from 'react';
import { Link } from 'gatsby';
import { css } from '@emotion/core';
import { palettes } from 'src/styles/constants/theme'
import { SwordIcon, ChartIcon } from 'src/components/icons'
import { Divider, IconList, NestedLists } from 'src/components';
import tw from 'twin.macro';
import { ToggleDarkMode } from 'src/components/toggleDarkMode';

const classes = {
  root: css({
    top: 0,
    left: 0,
    right: 'auto',
    display: 'flex',
    flex: '1 0 auto',
    flexDirection: 'column',
    width: '240px',
    height: '100%',
    outline: 0,
    zIndex: 1200,
    position: 'fixed',
    overflowY: 'auto',
    borderRight: `1px solid ${palettes.divider}`,
  }),
  listHeader: tw`
    sticky z-10 -top-px px-4 leading-20 font-bold bg-white dark:bg-gray-800 font-sans
  `,
}

interface drawerProps {
  drawerWidth: number,
  open: boolean,
  handleDrawerChange: () => void,
}

const Header = (props: drawerProps) => {

  return(
    <nav css={[classes.root,
              tw`transition ease-out`,
              props.open ? tw`transform-none` : css`transform: translateX(-${props.drawerWidth}px); visibility: hidden;`]}>
      <div tw="flex py-0 px-3 min-h-22 items-center justify-between">
        <div tw="ml-2">
          <div tw="text-gray-700">
            <Link to="/" tw="no-underline">SIN - SINoALICE</Link>
          </div>
          <div tw="text-lg text-gray-500">v0.0.1</div>
        </div>
        <div tw="p-4 rounded-full cursor-pointer hover:bg-hover dark:hover:bg-darkhover transition transition-background"
             onClick={() => props.handleDrawerChange()}>
          <svg tw="w-8 h-8" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke="currentColor">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 19l-7-7 7-7" />
          </svg>
        </div>
      </div>
      <Divider />
      <div css={classes.listHeader}>Calculators</div>
      <NestedLists
          topIcon={<SwordIcon />}
          topText={""}
          lists={[{icon: <SwordIcon />, text: "", link: "/"},
                  {icon: <SwordIcon />, text: "", link: "/"}]}
      />
      <NestedLists
          topIcon={<SwordIcon />}
          topText={""}
          lists={[{icon: <SwordIcon />, text: "", link: "/"},
                  {icon: <SwordIcon />, text: "", link: "/"}]}
      />
      <NestedLists
          topIcon={<SwordIcon />}
          topText={""}
          lists={[{icon: <SwordIcon />, text: "", link: "/"},
                  {icon: <SwordIcon />, text: "", link: "/"}]}
      />
      <IconList
        icon={<ChartIcon />}
        text={''}
        link={'/calc/combo'}
      />
      <IconList
        icon={<ChartIcon />}
        text={''}
        link={'/'}
      />
      <IconList
        icon={<ChartIcon />}
        text={''}
        link={'/calc/nightmare'}
      />
      <IconList
        icon={<ChartIcon />}
        text={''}
        link={'/calc/ship'}
      />
      <Divider />
      <div css={classes.listHeader}>Simulators</div>
      <IconList
        icon={<ChartIcon />}
        text={''}
        link={'/'}
      />
      <IconList
        icon={<ChartIcon />}
        text={''}
        link={'/'}
      />
      <IconList
        icon={<ChartIcon />}
        text={''}
        link={'/'}
      />
      <IconList
        icon={<ChartIcon />}
        text={''}
        link={'/'}
      />
      <Divider />
      <div css={classes.listHeader}>Generators</div>
      <IconList
        icon={<ChartIcon />}
        text={''}
        link={'/'}
      />
      <IconList
        icon={<ChartIcon />}
        text={''}
        link={'/'}
      />
      <Divider />
      <IconList
        icon={<ChartIcon />}
        text={''}
        link={'/'}
      />
      <IconList
        icon={<ChartIcon />}
        text={'Contact'}
        link={'/'}
      />
      <IconList
        icon={<ChartIcon />}
        text={'Credits'}
        link={'/'}
      />
      <Divider />
      <div tw="flex p-3 justify-between">
        <div tw="p-4 rounded-full cursor-pointer hover:bg-hover dark:hover:bg-darkhover transition transition-background"
             onClick={() => props.handleDrawerChange()}>
          <svg tw="w-8 h-8" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke="currentColor">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M10.325 4.317c.426-1.756 2.924-1.756 3.35 0a1.724 1.724 0 002.573 1.066c1.543-.94 3.31.826 2.37 2.37a1.724 1.724 0 001.065 2.572c1.756.426 1.756 2.924 0 3.35a1.724 1.724 0 00-1.066 2.573c.94 1.543-.826 3.31-2.37 2.37a1.724 1.724 0 00-2.572 1.065c-.426 1.756-2.924 1.756-3.35 0a1.724 1.724 0 00-2.573-1.066c-1.543.94-3.31-.826-2.37-2.37a1.724 1.724 0 00-1.065-2.572c-1.756-.426-1.756-2.924 0-3.35a1.724 1.724 0 001.066-2.573c-.94-1.543.826-3.31 2.37-2.37.996.608 2.296.07 2.572-1.065z" />
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 12a3 3 0 11-6 0 3 3 0 016 0z" />
          </svg>
        </div>
        <ToggleDarkMode />
      </div>
    </nav>
)};

export default Header;